import React from "react";
import animine from "./style/animine.mp4";
import "./style/projects.css";

function Project() {
  return (
    <div className="projects">
      <u>
        <h2
          style={{
            padding: "20px",
            fontWeight: "500",
            fontSize: "24px",
          }}
        >
          Personal Projects
        </h2>
      </u>
      <h3>Animine</h3>
      <div className="project">
        <div>
          <video className="project-iframe" id="animine" autoPlay loop muted>
            <source src={animine} />
          </video>
        </div>
        <div>
          <h3> &#8811; Animine is a low level clone of myanimelist.net</h3>
          <h3>
            &#8811; Technologies used: React JS, Html , Css, Javascript, React
            Hooks
          </h3>
          <h3>
            &#8811; What it does is , it fetches data from jikan api according
            to what page we are in and then display that on the screen. If any
            of the card been clicked it fetches that specific anime data and
            show it with a div sliding up. All the styling were done using pure
            css without any libraries.
          </h3>
          <h3>
            visit the website here :{" "}
            <a
              target="_blank"
              rel="noreferrer"
              href="https://animine.netlify.app/"
            >
              https://animine.netlify.app/
            </a>
          </h3>
        </div>
      </div>
      <hr />
      <h3>Animine</h3>
      <div className="project">
        <div>
          <h3> &#8811; Animine is a low level clone of myanimelist.net</h3>
          <h3>
            &#8811; Technologies used: React JS, Html , Css, Javascript, React
            Hooks
          </h3>
          <h3>
            &#8811; What it does is , it fetches data from jikan api according
            to what page we are in and then display that on the screen. If any
            of the card been clicked it fetches that specific anime data and
            show it with a div sliding up. All the styling were done using pure
            css without any libraries.
          </h3>
          <h3>
            visit the website here :{" "}
            <a
              target="_blank"
              rel="noreferrer"
              href="https://animine.netlify.app/"
            >
              https://animine.netlify.app/
            </a>
          </h3>
        </div>
        <div>
          <video className="project-iframe" id="animine" autoPlay loop muted>
            <source src={animine} />
          </video>
        </div>
      </div>
    </div>
  );
}

export default Project;
